<template>
  <div class="register-submit">
    <div class="card">
      <h1 style="font-weight: bold; margin-bottom: 40px; font-size: 18px">
        填写申请人信息
      </h1>
      <div class="zone inputZone1">
        <p class="title"><label for="mobile">手机</label></p>
        <input
          type="text"
          id="mobile"
          placeholder="请填写申请人手机号"
          v-model="mobile"
          ref="autofocusInput"
        />
      </div>
      <div class="zone inputZone2">
        <p class="title"><label for="email">邮箱</label></p>
        <input
          type="text"
          id="email"
          placeholder="请填写申请人邮箱"
          v-model="email"
          ref="passwordInput"
        />
      </div>

      <div class="zone button-zone">
        <button class="submit-button" @click="submitClick">注 册</button>
      </div>
    </div>
  </div>
</template>

<script>
import { decode_ez } from "../../../common/utils";
import { $sendEmail } from "../../../network/common/common";
export default {
  name: "e-SubmitProvider",
  activated() {
    /* if(this.mobile !== '') return;
    this.$refs.autofocusInput.focus(); */
  },
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isStaff") === "true") {
      window.localStorage.setItem("currentIndex010001", 5);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010001");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
    }
  },
  data() {
    return {
      mobile: "",
      email: "",
    };
  },
  methods: {
    async submitClick() {
      // 检验手机号和邮箱是否符合要求
      if (this.mobile === "") {
        this.$toast.error("手机号不能为空", 240);
        return;
      }
      if (this.email === "") {
        this.$toast.error("邮箱不能为空", 240);
        return;
      }
      // 正则有问题
      // const regExp1 = /^(0|\+86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      const regExp1 =
        /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
      const mobileFlag = regExp1.test(this.mobile);
      if (!mobileFlag) {
        this.$toast.error("请输入正确的手机号", 240);
        return;
      }
      const regExp2 =
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      const emailFlag = regExp2.test(this.email);
      if (!emailFlag) {
        this.$toast.error("请输入正确的邮箱", 240);
        return;
      }

      const response = await $sendEmail({
        mobile: this.mobile,
        email: this.email,
      });
      this.mobile = "";
      this.email = "";

      if (response.Code === 200 && response.msg === "success") {
        this.$toast.success("邮件发送成功", 240);
      } else {
        this.$toast.error("邮件发送失败", 240);
      }
    },
  },
};
</script>

